import * as React from 'react';
import { createComponent } from '@lit-labs/react';
import type { EventName } from '@lit-labs/react';
import { Picker as SpPicker } from '@spectrum-web-components/picker';

import '@spectrum-web-components/picker/sp-picker.js';

export const Picker = createComponent({
    displayName: 'Picker',
    elementClass: SpPicker,
    react: React,
    tagName: 'sp-picker',
    events: {
        change: 'change' as EventName<Event>, // Announces that the `value` of the element has changed
        spOpened: 'sp-opened' as EventName<Event>, // Announces that the overlay has been opened
        spClosed: 'sp-closed' as EventName<Event>, // Announces that the overlay has been closed
    },
});

export type PickerType = EventTarget & SpPicker;
